import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';

import { withStyles } from '@material-ui/core/styles';
import {
  Block,
  Done,
  DeleteForever,
} from '@material-ui/icons';
import {
  Grid,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Button,
} from '@material-ui/core';


const CustomTableCell = withStyles(theme => ({
  head: {
    backgroundColor: '#3f51b5',
    color: theme.palette.common.white,
    fontSize: 19,
    fontWeight: 500,
  },
  body: {
    fontSize: 14,
    fontWeight: 400,
  },
}))(TableCell);

const styles = theme => ({
  root: {
    width: '98%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 500,
  },
  row: {
    '&:nth-of-type(even)': {
      backgroundColor: '#dcdff1',
    },
    textAlign: 'justify',
  },
  rightIcon: {
    marginLeft: 5,
  },
});

//mocking opinions
let _id = 0;

function createMockedOpinions(place, author, text, date, likes, tags, isBlocked = false) {
  _id++;
  return { _id, place, author, text, date, likes, tags, isBlocked };
}

const rows = [
  createMockedOpinions('Rysy', 'Tomasz_Kowalski', 'Dzisiaj piękna pogoda i świetna widoczność - warto zabrać aparat', '29-05-2019', 13, 'słonecznie, bezpiecznie, dobra widoczność'),
  createMockedOpinions('Rysy', 'prz.szopa96', 'Bardzo ślisko po deszczu, trzeba uważać', '30-05-2019', 2, 'ślisko deszcz'),
  createMockedOpinions('Giewont', 'weronka341', 'Pogoda dopisała, ale trasa bardzo zatłoczona', '29-05-2019', 3, 'słonecznie, tłok'),
  createMockedOpinions('Łomnica', 'oski997', 'Na szczycie niesamowita mgła, radzę zachować ostrożność', '27-05-2019', 5, 'mgła, słaba widoczność'),
  createMockedOpinions('Łomnica', 'swieradOlek', 'Trzeba zachować szczególną ostrożność z uwagi na mgłę.', '28-05-2019', 0, 'mgła, słaba widoczność', true),
  createMockedOpinions('Świnica', 'ptr.wos', 'Niestety ze względu na bardzo ulewny deszcz nie podjęliśmy się zdobycia szczytu.', '30-05-2019', 69, 'deszcz, ślisko, pochmurno'),
  createMockedOpinions('Giewont', 'weronka341', 'Pogoda dopisała, ale trasa bardzo zatłoczona', '29-05-2019', 7, 'słonecznie, tłok'),
  createMockedOpinions('Giewont', 'weronka341', 'Pogoda dopisała, ale trasa bardzo zatłoczona', '29-05-2019', 1, 'słonecznie, tłok', true),
];

class OpinionsTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opinions: [],
      blockedOpinions: [],
    };
  }

  async componentDidMount() {
    if (this.props.mocked) {
      this.setState({
        opinions: rows.filter(row => !row.isBlocked),
        blockedOpinions: rows.filter(row => row.isBlocked)
      });
    } else {
      const token = `Bearer ${await localStorage.getItem('token')}`;
      try {
        const opinionsResponse = await axios.get('admin/opinions',
          {
            headers: {
              Authorization: token
            }
          });

        // TODO: Connect in the future if reporting functionality present on API
        // eslint-disable-next-line
        const opinions = opinionsResponse.data.opinions;
        const blockedOpinionsResponse = await axios.get('admin/users/blocked',
          {
            headers: {
              Authorization: token
            }
          });
        const blockedOpinions = blockedOpinionsResponse.data.blockedOpinions;
        this.setState({ blockedOpinions });
      } catch (error) {
        console.error(error.response.data.message);
      }
    }
  }

  async confirmBlocking(id) {
    if (window.confirm('Are you sure you want to block this opinion?')) {
      if (this.props.mocked) {
        rows[id - 1].isBlocked = true;
        this.setState({
          opinions: rows.filter(row => !row.isBlocked),
          blockedOpinions: rows.filter(row => row.isBlocked)
        });
      } else {
        const token = `Bearer ${await localStorage.getItem('token')}`;
        try {
          const response = await axios.put(`admin/opinions/${id}/block`,
            {},
            {
              headers: {
                Authorization: token
              }
            });

          if (response.data.message === 'Opinion blocked') {
            const { blockedOpinions } = this.state;
            blockedOpinions.push(response.data.opinion);
            this.setState({
              opinions: this.state.opinions.filter(opinion => opinion._id !== id),
              blockedOpinions,
            });
          }

        } catch (error) {
          console.error(error);
        }
      }
    }
  }

  async confirmUnblocking(id) {
    if (window.confirm('Are you sure you want to unblock this opinion?')) {
      if (this.props.mocked) {
        rows[id - 1].isBlocked = false;
        this.setState({
          opinions: rows.filter(row => !row.isBlocked),
          blockedOpinions: rows.filter(row => row.isBlocked)
        });
      } else {
        const token = `Bearer ${await localStorage.getItem('token')}`;
        try {
          const response = await axios.put(`admin/opinions/${id}/unblock`,
            {},
            {
              headers: {
                Authorization: token
              }
            });

          if (response.data.message === 'Opinion unblocked') {
            const { opinions } = this.state;
            opinions.push(response.data.opinion);
            this.setState({
              opinions,
              blockedOpinions: this.state.blockedOpinions.filter(opinion => opinion._id !== id)
            });
          }
        } catch (error) {
          console.error(error.response.data.message);
        }
      }
    }
  }

  async deleteOpinion(id) {
    if (window.confirm('Are you sure you want to delete this opinion?')) {
      if (this.props.mocked) {
        rows.splice(id - 1, 1);
        this.setState({
          opinions: rows.filter(row => !row.isBlocked),
          blockedOpinions: rows.filter(row => row.isBlocked)
        });
      } else {
        const token = `Bearer ${await localStorage.getItem('token')}`;
        try {
          const response = await axios.delete(`admin/opinions/${id}`,
            {
              headers: {
                Authorization: token
              }
            });
          if (response.data.message === 'Opinion deleted') {
            if (this.props.blocked) {
              this.setState({
                blockedOpinions: this.state.blockedOpinions.filter(opinion => opinion._id !== id)
              });
            } else {
              this.setState({
                opinions: this.state.opinions.filter(opinion => opinion._id !== id)
              });
            }
          }
          window.location.reload();
        } catch (error) {
          console.error(error.response.data.message);
        }
      }
    }
  }

  renderTableHead() {
    return (
      <TableHead>
        <TableRow>
          <CustomTableCell>place</CustomTableCell>
          <CustomTableCell align='center'>author</CustomTableCell>
          <CustomTableCell align='center'>text</CustomTableCell>
          <CustomTableCell align='center'>date</CustomTableCell>
          <CustomTableCell align='center'>likes</CustomTableCell>
          <CustomTableCell align='center'>tags</CustomTableCell>
          <CustomTableCell align='center'>
            {`${this.props.blocked ? 'unblock' : 'blocked'} | delete`}
          </CustomTableCell>
        </TableRow>
      </TableHead>
    );
  }

  renderTableRow(opinion, onActionClick) {
    const { classes } = this.props;
    return (
      <TableRow className={classes.row} key={opinion._id}>
        <CustomTableCell size='small'>
          {opinion.place}
        </CustomTableCell>
        <CustomTableCell align='center'>{opinion.author}</CustomTableCell>
        <CustomTableCell align='justify'>{opinion.text}</CustomTableCell>
        <CustomTableCell align='center'>{opinion.date}</CustomTableCell>
        <CustomTableCell align='center'>{opinion.likes}</CustomTableCell>
        <CustomTableCell align='center'>{opinion.tags}</CustomTableCell>
        <CustomTableCell align='center'>
          <Button
            value={opinion._id}
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={onActionClick}
          >
            {!this.props.blocked
              ? <React.Fragment>
                Block
                <Block className={classes.rightIcon}/>
              </React.Fragment>
              : <React.Fragment>
                Unblock
                <Done className={classes.rightIcon}/>
              </React.Fragment>
            }
          </Button>
          <Button
            value={opinion._id}
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={() => this.deleteOpinion(opinion._id)}
          >
            Delete
            <DeleteForever className={classes.rightIcon}/>
          </Button>
        </CustomTableCell>
      </TableRow>
    );
  }

  renderEmptyOpinionsTableBody() {
    return (
      <TableRow className={this.props.classes.row}>
        <CustomTableCell>No blocked opinions</CustomTableCell>
      </TableRow>
    );
  }

  renderBlockedOpinions() {
    return this.state.blockedOpinions.map(opinion =>
      this.renderTableRow(opinion, () => this.confirmUnblocking(opinion._id))
    );
  }

  renderNonBlockedOpinions() {
    return this.state.opinions.map(opinion =>
      this.renderTableRow(opinion, () => this.confirmBlocking(opinion._id))
    );
  }

  renderTableBody() {
    if (!this.state.blockedOpinions && this.props.blocked) {
      return this.renderEmptyOpinionsTableBody();
    } else if (this.props.blocked) {
      return this.renderBlockedOpinions();
    } else {
      return this.renderNonBlockedOpinions();
    }
  }

  render() {
    const { classes } = this.props;

    return (
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
      >
        <Typography variant="h3">{`${this.props.blocked ? 'Blocked' : 'All'} opinions`}</Typography>
        <Paper className={classes.root}>
          <Table className={classes.table} padding='checkbox'>
            {this.renderTableHead()}
            <TableBody>
              {this.renderTableBody()}
            </TableBody>
          </Table>
        </Paper>
      </Grid>
    );
  }
}

OpinionsTable.propTypes = {
  blocked: PropTypes.bool,
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(OpinionsTable);
